import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import AddCategoryModal from './AddCategoryModal';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http/index';
import { Table } from '../../../components';
import {
  getAllCategorySaga,
  editCategoryStatusSaga,
  deleteCategorySaga,
} from '../../../store/actions';

import DeleteModel from '../../../components/UI/Model/DeleteModel';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

// eslint-disable-next-line no-unused-vars
const MenuCategories = props => {
  const [modelOpen, setModelOpen] = useState(false);
  const [mounted, setMounted] = useState(false);
  const [category, setCategory] = useState('');
  const [isEdit, setEditCategory] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  const [deleteId, setDeleteId] = useState('');
  const [modelDeleteOpen, setModelDeleteOpen] = useState(false);

  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const {
    categories: { list: allData, totalRecords },
    errorMsg,
  } = useSelector(state => state.dish);
  const { open } = useSelector(state => state.modal);

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllCategorySaga({ columnName, orderBy, search, skip: 0, limit }),
      );
    } else {
      dispatch(
        getAllCategorySaga({ columnName, orderBy, search, skip, limit }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const statusAction = data => {
    dispatch(editCategoryStatusSaga({ id: data.id, status: data.status }));
  };
  const openEditCategoryModal = data => {
    setCategory(data);
    setEditCategory(true);
    setModelOpen(true);
  };

  const deleteAction = payload => {
    setModelDeleteOpen(true);
    setDeleteId(payload._id);
  };

  const tableFields = [
    {
      displayName: 'Image',
      key: 'thumbnail',
      isMedia: true,
      mediaType: 'image',
      // baseURL: `${process.env.REACT_APP_END_POINT_IMG_URL}/category/`,
      noSort: true,
    },
    {
      displayName: 'Name',
      key: 'name',
    },
    {
      displayName: 'Status',
      key: 'status',
      isStatus: true,
      positiveValue: true,
      action: statusAction,
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-pencil" aria-hidden="true" />,
          action: openEditCategoryModal,
        },
        {
          view: <i className="fa fa-trash" aria-hidden="true" />,
          action: deleteAction,
        },
      ],
    },
  ];

  const linkObj = {};

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  const confirmDeleteAction = () => {
    const id = deleteId;
    setDeleteId('');
    dispatch(deleteCategorySaga({ id, tableFilter, setModelDeleteOpen }));
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Category</h2>
              </div>
              <div className="text-right  d-flex-right">
                <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  data-toggle="modal"
                  data-target="#send-modal"
                  onClick={() => {
                    setEditCategory(false);
                    setModelOpen(true);
                  }}
                >
                  Add Category
                </button>
              </div>
              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  link={linkObj}
                  linkPropsName="_id"
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <AddCategoryModal
          setModelOpen={setModelOpen}
          tableFilter={tableFilter}
          category={category}
          isEdit={isEdit}
        />
      )}
      {modelDeleteOpen && (
        <DeleteModel
          setModelOpen={setModelDeleteOpen}
          submitBtnText="Yes"
          submitBtnHandler={confirmDeleteAction}
          cancelBtnText="No"
          closeModel={setModelDeleteOpen}
        />
      )}
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(MenuCategories, axios);
