import React, { useState, useEffect } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import Model from '../../../components/UI/Model/Model';
import { addCategorySaga, editCategorySaga } from '../../../store/actions';
import { CATEGORYNAME_REQUIRED } from '../../../constants/errorConstants';
// eslint-disable-next-line no-unused-vars
const AddCategoryModal = props => {
  const { tableFilter } = props;
  const [isDisable, setIsDisable] = useState(false);
  const [photoURL, setPhotoURL] = useState(null);
  const { errorMsg } = useSelector(state => state.dish);
  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };
  useEffect(() => {
    if (props.isEdit) setPhotoURL(props.category.thumbnail);
    //return () => setMounted(false);
  }, []);
  const dispatch = useDispatch();

  const validationSchema = Yup.object({
    name: Yup.string().trim().required(CATEGORYNAME_REQUIRED),
    photo: Yup.string(),
  });

  const submitBtnHandler = values => {
    setIsDisable(true);
    if (props.isEdit) {
      dispatch(
        editCategorySaga({
          ...values,
          id: props.category._id,
          tableFilter,
          closeModel,
          setIsDisable,
        }),
      );
    } else {
      dispatch(
        addCategorySaga({
          ...values,
          tableFilter,
          closeModel,
          setIsDisable,
        }),
      );
    }
  };

  // const getImage = evt => {
  //   const { files } = evt.target;
  //   setcategoryImage(files[0]);
  // };

  return (
    <Formik
      initialValues={{
        name: props.isEdit ? props.category.name : '',
        photo: '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        setFieldValue,
      }) => (
        <Model
          closeModel={closeModel}
          headerTitle={props.isEdit ? 'Edit Category' : 'Add Category'}
          submitBtnText={props.isEdit ? 'Edit' : 'Add'}
          submitBtnHandler={handleSubmit}
          isDisable={isDisable}
          errorMsg={errorMsg}
        >
          <form onSubmit={handleSubmit}>
            <div className="from-group text-left">
              <h4 className="required-field">Name of category:</h4>
              <input
                type="text"
                className="form-control"
                name="name"
                value={values.name}
                // onChange={e => setName(e.target.value)}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.name && touched.name && errors.name}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="">File name:</h4>
              <input
                type="file"
                name="photo"
                id="photo"
                accept="image/x-png ,image/png, image/jpeg, images/gif"
                onChange={e => {
                  const { files } = e.target;
                  if (files[0]) {
                    if (files[0].type.includes('image')) {
                      const filename = files[0].name;
                      const fileExtension = filename.substr(
                        filename.lastIndexOf('.') + 1,
                      );
                      if (
                        fileExtension.toLowerCase() === 'png' ||
                        fileExtension.toLowerCase() === 'jpg' ||
                        fileExtension.toLowerCase() === 'gif' ||
                        fileExtension.toLowerCase() === 'jpeg'
                      ) {
                        setFieldValue('photo', e.currentTarget.files[0]);
                        setPhotoURL(URL.createObjectURL(files[0]));
                      }
                    }
                  }
                }}
                // onChange={handleChange}
                onBlur={e => {
                  const { files } = e.target;
                  if (files[0]) {
                    if (files[0].type.includes('image')) {
                      const filename = files[0].name;
                      const fileExtension = filename.substr(
                        filename.lastIndexOf('.') + 1,
                      );
                      if (
                        fileExtension.toLowerCase() === 'png' ||
                        fileExtension.toLowerCase() === 'jpg' ||
                        fileExtension.toLowerCase() === 'gif' ||
                        fileExtension.toLowerCase() === 'jpeg'
                      ) {
                        setFieldValue('photo', e.currentTarget.files[0]);
                        setPhotoURL(URL.createObjectURL(files[0]));
                      }
                    }
                  }
                }}
              />
              {photoURL && (
                <img className="photo-preview" alt="dishImage" src={photoURL} />
              )}
              <div className="error-message">
                {errors.photo && touched.photo && errors.photo}
              </div>
            </div>
          </form>
        </Model>
      )}
    </Formik>
  );
};

export default AddCategoryModal;
